'use client';
import React, { useState } from 'react';
import Sidebar from './sidebar';
import GoogleMapsComponent from './map';
import Topbar from './topbar';

export default function Home() {
  const [isPinMode, setIsPinMode] = useState(false);
  const [hasActivePin, setHasActivePin] = useState(false); // Track if a pin exists

  const handleClear = () => {
    setHasActivePin(false);
    // We will handle the actual map removal in the Map component
  };

  return (
    <div className="flex flex-col h-screen">
      <Sidebar>
        <PinButton onClick={() => setIsPinMode(true)} isPinning={isPinMode} />
        <ClearPinButton onClick={handleClear} visible={hasActivePin} />
      </Sidebar>

      <GoogleMapsComponent 
        isPinMode={isPinMode} 
        setPinMode={setIsPinMode} 
        hasActivePin={hasActivePin} 
        setHasActivePin={setHasActivePin}
      />
    </div>
  );
}